import React from 'react'
import { useEffect } from 'react'
import AOS from 'aos'
import 'aos/dist/aos.css'

export default function Resume() {
    useEffect(() => {
        AOS.init({ duration: 1000 })
    }, [])

    return <>
        {/* <Praticles /> */}
        <div className=' text-white  ' id='resume' style={{ overflow: "hidden" }} >
            {/* <p className='text-6xl  text-center pt-28 pb-20 ' >Resume</p>
            <div className='text-4xl  text-center pb-14 ' >My education</div>
            <div className='d-sm-flex gap-5 pb-20 mx-20' > */}
            <p className='text-6xl  text-center pt-28 pb-20 ' data-aos="fade-up">Resume</p>
            <div className='text-4xl  text-center pb-14 ' data-aos="fade-up">My education</div>
            <div className='d-sm-flex gap-5 pb-20 mx-20' data-aos="fade-up">
                <div className=' col-sm-6 bg-gray-400 pt-10 pr-10 pl-10   rounded-xl' data-aos="fade-right">
                    <p className='bg-green-600 p-1 rounded-xl text-center '> 2018-2021</p>
                    <p className='text-black text-4xl pt-4 mt-3'>Bachelor Degree</p>
                    <p className='text-black text-2xl pt-3'> Bachelor of Computer Application</p>
                    <p className='text-xl text-black pt-3 pb-20'>Dr. Babasaheb Ambedkar Marathwada University</p>
                </div>
                <div className='col-sm-6 bg-gray-400 pt-10 pr-10 pl-10 mt-5 mt-sm-0 rounded-xl' data-aos="fade-left">
                    <p className='bg-green-600 p-1 rounded-xl text-center '> 2021-2023</p>
                    <p className='text-black text-4xl pt-4 mt-3'>Master Degree</p>
                    <p className='text-black text-2xl pt-3'> Master of Computer Application</p>
                    <p className='text-xl text-black pt-3 pb-20'>Dr. Babasaheb Ambedkar Marathwada University</p>
                </div>

            </div>


        </div>
    </>
}
