import React from 'react'

// import photo from "./image/photo1.png"
import { useState, useEffect } from "react";
import {
    Navbar,
    MobileNav,
    Typography,
    Button,
    IconButton,
} from "@material-tailwind/react";



export default function NavbarItem() {
    const [openNav, setOpenNav] = useState(false);

    useEffect(() => {
        window.addEventListener(
            "resize",
            () => window.innerWidth >= 960 && setOpenNav(false)
        );
    }, []);

    const navList = (
        <ul className="mb-4 mt-2 flex flex-col gap-2 lg:mb-0 lg:mt-0 lg:flex-row lg:items-center lg:gap-6">
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5
                
                "
            >
                <a href="#home" className="flex items-center">
                    HOME
                </a>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5"
            >
                <a href="#about" className="flex items-center">
                    ABOUT
                </a>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5"
            >
                <a href="#resume" className="flex items-center">
                    RESUME
                </a>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5"
            >
                <a href="#skills" className="flex items-center">
                    SKILLS
                </a>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5"
            >
                <a href="#projects" className="flex items-center">
                    PROJECTS
                </a>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="white"
                className="p-1 text-lg mt-2 mr-5"
            >
                <a href="#contact" className="flex items-center">
                    CONTACT
                </a>
            </Typography>

        </ul>
    );
    return <>
        <Navbar className="sticky top-0 bg-black mx-0  py-2 px-4 lg:px-8 lg:py-4 " style={{ zIndex: 5000 }} >
            <div className="container mx-0 flex items-center justify-between ">
                <Typography
                    as="a"
                    href="#"
                    color='white'
                    variant="small"
                    className="mr-4 cursor-pointer py-1.5 font-normal text-3xl"
                >
                    <span>Pratiksha Kurkure</span>
                </Typography>
                <div className="hidden lg:block">{navList}</div>
                {/* <Button variant="gradient" size="sm" className="hidden lg:inline-block">
                    <span>Buy Now</span>
                </Button> */}
                <IconButton
                    variant="text"
                    className="ml-auto h-6 w-6 text-inherit hover:bg-transparent focus:bg-transparent active:bg-transparent lg:hidden"
                    ripple={false}
                    onClick={() => setOpenNav(!openNav)}
                >
                    {openNav ? (
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            className="h-6 w-6"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                            strokeWidth={2}
                        >
                            <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                d="M6 18L18 6M6 6l12 12"
                            />
                        </svg>
                    ) : (
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            className="h-6 w-6"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth={2}
                        >
                            <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                d="M4 6h16M4 12h16M4 18h16"
                            />
                        </svg>
                    )}
                </IconButton>
            </div>
            <MobileNav open={openNav}>
                {navList}
                {/* <Button variant="gradient" size="sm" fullWidth className="mb-2">
                    <span>Buy Now</span>
                </Button> */}
            </MobileNav>
        </Navbar>










    </>
}
