import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

//works as action
export const getAllContacts = createAsyncThunk("contact/add", async (contactData, { rejectWithValue, getstate }) => {
    console.log("called", contactData);
    try {
        const { data } = await axios.post("http://localhost:5000/contact/add", contactData)

        console.log(data.result);
        return data.result
    } catch (error) {
        console.log(error);
        return rejectWithValue(error)
    }
})
