import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { getAllContacts } from '../contatSlice'
import AOS from 'aos'
import 'aos/dist/aos.css'
import { useEffect } from 'react'

export default function Contact() {
    const dispatch = useDispatch()
    const [contactData, setcontactData] = useState({
        name: "",
        email: "",
        desc: ""
    })
    const handleSubmit = () => {
        dispatch(getAllContacts(contactData))
        alert("Response Submitted Succesfully")
        // console.log(contactData, "data");
    }
    useEffect(() => {
        AOS.init({ duration: 2000 })
    }, [])

    return <>
        {/* <Praticles /> */}
        <div className='bg-black' id='contact'>
            <div className='pt-28 text-5xl  ' data-aos="fade-up">
                <div className='text-white text-center '>GET IN TOUCH</div>
            </div>
            <div className='d-sm-flex gap-5 mt-28 text-white mx-20 pb-20 ' data-aos="fade-up">
                <div className='col-sm-3'><p className='text-xl font-bold mb-2'>Phone:</p>
                    <i className="bi bi-whatsapp"></i><span className='ml-3 '>7666784920</span>
                    <p className='text-xl font-bold my-3'>Email:</p>
                    <i className="bi bi-envelope"></i><span className='ml-3'>pratikshakurkure850@gmail</span>
                    <p className='text-xl font-bold my-3'>Instagram:</p>
                    <i className="bi bi-instagram"></i><span className='ml-3'>im_.pratiksha</span>
                    <p className='text-xl font-bold my-3'>Github:</p>
                    <i className="bi bi-github"></i><span className='ml-3'>https://github.com/Pratiksha-Kurkure</span>
                </div>
                <div className='col-sm-9 mt-5 mt-sm-0 '>
                    <p className=' text-xl'>If you have any suggestion, project or even you want to say Hello..
                        please fill out the form below and I will reply you shortly</p>
                    <div>
                        <div className='d-xl-flex   mt-4'>
                            <div className='col-sm-6'><p className=' text-lg'>Enter Name</p> <br />
                                <input type="text"
                                    id='name'
                                    onChange={e => { setcontactData({ ...contactData, name: e.target.value }) }}
                                    value={contactData.name} className='rounded-2xl h-10 bg-gray-400 w-80 text-black ' />
                            </div>
                            <div className='col-sm-6'><p className=' text-lg'>Enter Email</p><br /><input type="email"
                                id='email'
                                onChange={e => { setcontactData({ ...contactData, email: e.target.value }) }}
                                value={contactData.email} className='rounded-2xl h-10 bg-gray-400 w-80  ' />
                            </div>
                        </div>
                        <br />
                        <label htmlFor="" className='mt-8 text-lg'>Tell me more about your need....</label>
                        <input type="text"
                            id='moreinfo'
                            onChange={e => { setcontactData({ ...contactData, desc: e.target.value }) }}
                            value={contactData.moreinfo}
                            className='rounded-2xl h-28 bg-gray-400 w-full  ' />
                        <button className='btn btn-success rounded-2xl mt-8 text-center' onClick={handleSubmit}>Send Message</button>
                    </div>
                </div>
            </div>
            <div className='text-white py-9 px-20 text-center ' >
                <div>Copyright  2023 Pratiksha. All Rights Reserved.</div>
            </div>
        </div>
    </>
}
