import React from 'react'
import { motion } from "framer-motion"
import resumepdf from "./image/PRATIKSHA_KURKURE.pdf"
import Praticles from './Praticles'
const line1 = "I'm a Web Developer"
const sentence = {
    hidden: { opacity: 1 },
    visible: {
        opacity: 1,
        transition: {
            delay: 0.5,
            staggerChildren: 0.08,
        },
    },
}
const letter = {
    hidden: { opacity: 0, y: 50 },
    visible: {
        opacity: 1,
        y: 0,
    },
}
export default function Home() {
    return <div className='text-center mt-52 text-light' id='home'>
        {/* <h1 className='display-1'>Hi My Name is John</h1> */}
        {/* <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Tenetur repellendus, suscipit architecto doloribus aperiam aut officia enim voluptatum, distinctio exercitationem facere eos nulla in! Iusto quisquam aliquid illo ipsam architecto!</p> */}
        <div className=''><p className='text-3xl text-white'>WELCOME</p>
            <motion.p className='load-screen--message text-white text-6xl  font-bold mt-4 '
                variants={sentence}
                initial="hidden"
                animate="visible">
                {line1.split("").map((char, index) => {
                    return (
                        <motion.span key={char + "-" + index}
                            variants={letter}>{char}</motion.span>
                    )
                })}
            </motion.p>
            {/* <button className='btn btn-success rounded-full mt-10 mb-64'>Hire Me</button> */}
            <div className="header__buttons">
                <a href={resumepdf} className="btn btn-success rounded-full mt-10 mb-64" download>Download Resume
                </a>
            </div>
            {/* <div className="header__buttons">
                <a href="./image/A.pdf" className="btn btn-outline-success" download>Download Resume
                </a>
            </div> */}
        </div>
    </div>
}
