import AOS from 'aos'
import 'aos/dist/aos.css'
import { useEffect } from 'react'
export default function About() {
    useEffect(() => {
        AOS.init({ duration: 1000 })
    }, [])

    return <>
        <div className="text-light bg-black pb-20 pt-20" id='about'>
            <div className="text-5xl text-sm-6xl text-center pt-28 pb-20 " data-aos="fade-up">
                KNOW ME MORE
            </div>
            {/* <div className=' grid grid-cols-2 mx-20 pb-40' > */}
            <div className=' d-xl-flex   mx-20 pb-40' data-aos="fade-up">
                <div><p className='col-sm-8 text-4xl pb-10'>I'm <span className='text-green-600'>Pratiksha Kurkure</span> a Web Developer</p>
                    <p className='text-2xl'>I'm a UI Designer and Developer based in Aurangabad, India. I strives to build immersive and beautiful web
                        applications through carefully crafted code and user-centric design. Works in HTML, CSS, JavaScript, React, Redux. </p>
                </div>
                <div className='col-sm-4 mt-5 mt-xl-0 text-xl'>
                    <p className='mb-3'><span>Name: </span>Pratiksha Kurkure</p>
                    <hr />
                    <p className='my-3'><span>Email: </span>pratikshakurkure850@gmail.com</p>
                    <hr />
                    <p className='my-3'><span>Age: </span>22</p>
                    <hr />
                    <p className='my-3'><span>From: </span>Aurangabad, India</p>
                </div>
            </div></div></>
}
